/* Basic layout and design */
.container {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    color: #17a2b8;
    font-family: 'Arial', sans-serif;
  }
  
  .form-label {
    font-weight: bold;
    font-size: 1.1em;
  }
  
  input[type="number"], select, input[type="text"] {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  input[type="text"] {
    background-color: #f0f0f0;
    font-weight: bold;
  }
  
  select {
    background-color: #ffffff;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #28a745;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 1.1em;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #218838;
  }
  
  button:focus {
    outline: none;
  }
  
  /* Additional Styling for the Result */
  input[readonly] {
    color: #007bff;
    font-size: 1.2em;
    text-align: center;
  }
  
  /* Styling for the form elements */
  .form-control {
    margin-bottom: 20px;
  }
  
  select {
    font-size: 1em;
  }
  